*, *::after, *::before {
  box-sizing: border-box;
}

body {
  background-color: #e8e7e7;
  text-transform: uppercase;
}
.app-container {
 width: 700px;
 margin: 70px auto;
 border-radius: 6px;
}

/* HEADER */
header {
  color: #fff;
  padding: 5px 10px;
  text-align: center;
  display: flex;
  align-items: center;
  background-color: #8063fa;
  border-bottom: 2px  solid #d4caff;
  border-radius: 6px 6px 0 0;
}

.stats {
  display: flex;
  margin: 0 0 0 28px;
  font-weight: 400;
}

tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}

tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}

td {
  display: table-cell;
  vertical-align: inherit;
}

.stats td:first-child {
  text-align: right;
  font-weight: 400;
  letter-spacing: 2px;
  font-size: .7rem;
}

.stats td:last-child {
  text-align: left;
}

.header h1 {
  flex-grow: 1;
  font-size: 1.5rem;
  letter-spacing: 2px;
  font-weight: 400;
}

.remove {
  padding: 22px 10px;
  background: #5b45b9;
  width: 170px;
  margin: -5px -10px -5px 10px;
  border-radius: 0 6px 0 0;
}

.remove-btn {
  margin: 8px 5px;
  background-color: #44338d;
  border-radius: 5px;
  padding: 7px 8px;
  border: none;
  color: #fff;
  letter-spacing: 2px;
  font-weight: 700;
  text-shadow: none;
  text-transform: uppercase;
}

.remove-btn:hover {
  background: #fff;
  color: #44339d;
  cursor: pointer;
}

/* Todo */
.item {
  font-size: 1.2em;
  border-bottom: 2px solid #eee;
  letter-spacing: 2px;
  display: flex;
  background-color: #fff;
}

.item-check {
  width: 50px;
  line-height: 3.5rem;
}

.item-check input {
  margin-left: 20px;
}

.item p {
  margin: 0;
  flex-grow: 5;
  line-height: 3.5rem;
  color: #2f2366;
}

.item-delete {
  flex-grow: 1;
  font-size: 1.15em;
  line-height: 3.5rem;
  border: none;
  outline: none;
  color: #ef5350;
  visibility: hidden;
  cursor: pointer;
  background: none;
  padding-right: 40px;
  transition: background-color .2s ease, color .2s ease;
}

.item:hover .item-delete {
  visibility: visible;
}


/* Add Todo */
form {
  display: flex;
  background-color: #8063fa;
}

input[type=text] {
  -ms-flex-positive: 1;
  flex-grow: 1;
  border-width: 0 0 1px;
  margin: 15px 10px 15px 15px;
  padding: 10px;
  border-radius: 5px;
  background-color: #5b45b9;
  border-style: none;
  text-shadow: none;
  text-transform: uppercase;
  color: #c7bcf8;
  letter-spacing: 2px;
  outline: none;
}

input[type=text]::placeholder {
  color: #c7bcf8;
}

form button {
  width: 80px;
  outline: none;
  transition: background-color .2s ease, color .2s ease;
  font-size: .6em;
  margin: 15px 0 15px 15px;
  padding: 10px;
  background-color: #44338d;
  border-radius: 5px;
  border: none;
  color: #fff;
  letter-spacing: 2px;
  font-weight: 700;
  text-shadow: none;
  text-transform: uppercase;
  cursor: pointer;
}

form button:hover {
  background-color: #fff;
  color: #44338d;
}

.strike {
  text-decoration: line-through;
}